@import "../search-bar/search-default-small.css";
@import "../search-bar/search-bar-active.css";

.search-full{
    width: 300px;
    height: 210px;
    background: #FFFFFF;
    border: 1px solid #ECEBED;
    box-sizing: border-box;
    box-shadow: 0px 7px 64px rgba(0, 0, 0, 0.07);
    border-radius: 6px;

}
.search-full__search{
    margin: 0;
    z-index: 1;
    font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen-Sans,Ubuntu,Cantarell,"Helvetica Neue",sans-serif;
    line-height: 15px;
}
.search-full__list{
    padding: 0;
    box-sizing: border-box;
    width: 300px;
    margin: -10px 0px 0px 0px;
    height: 182px;
    z-index: 10;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    background-color: #fff;
}
.search-full__line{
    width: 270px;
    margin-top: 0px;
    margin-left: 14px;
}
.search__select{
    width: 100%;
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items:flex-start;
    list-style: none; 
    margin-top: 0px;
    margin-bottom: 16px;
}
.search-select__text{
    width: 276px;
    padding: 9px;
    display: flex;
    justify-content: flex-start;
    font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen-Sans,Ubuntu,Cantarell,"Helvetica Neue",sans-serif;
    line-height: 15px;
    padding-left:16px;
    height: 20px;
    color: #3F3356;

}

.text__version{
    mix-blend-mode: normal;
    opacity: 0.3;
}
.search-select__text:hover{
    background: rgba(0, 0, 0, 0.07);
}